   &__cloud {\n      &-front {\n        /* animation: 30s change-position__cloud infinite alternate ease-in-out; */\n        @include animation-initializer(\n          30s,\n          change-position__cloud,\n          infinite,\n          alternate,\n          ease-in-out\n        );\n      }\n      &-back {\n        /* animation: 34s change-position__cloud--reverse infinite alternate ease-in-out; */\n\n        @include animation-initializer(\n          34s,\n          change-position__cloud--reverse,\n          infinite,\n          alternate,\n          ease-in-out\n        );\n      }\n    }\n\n    &__grass {\n      /* animation: 34s opacity-change__grass infinite alternate linear; */\n      @include animation-initializer(\n        1s,\n        opacity-change__grass,\n        infinite,\n        alternate,\n        ease-in-out\n      );\n    }\n\n    &__horse {\n      &-right {\n        transform-origin: center center;\n        /* animation: 34s brightness-change__horse infinite alternate linear; */\n        @include animation-initializer(\n          2s,\n          brightness-change__horse,\n          infinite,\n          alternate,\n          ease-in-out\n        );\n      }\n      &-left {\n        transform-origin: center center;\n        /* animation: 34s brightness-change__horse infinite alternate linear; */\n        @include animation-initializer(\n          2s,\n          brightness-change__horse,\n          infinite,\n          alternate,\n          ease-in-out\n        );\n      }\n    }\n\n    &__tower-bubble {\n      /* animation: 34s blur-change__bubble infinite alternate linear; */\n      @include animation-initializer(\n        2s,\n        blur-change__bubble,\n        infinite,\n        alternate,\n        ease-in\n      );\n    }\n\n    &__container {\n      width: 50%;\n      margin: auto;\n\n      @media only screen and (max-width: 768px) {\n        width: 100%;\n      }\n\n      &-title {\n        text-align: center;\n        border-bottom: 1px solid $color-border;\n        a {\n          text-decoration: none;\n          transition: all 0.5s ease-in-out;\n          &:hover {\n            font-weight: normal;\n          }\n        }\n      }\n    }\n  }\n}\n\n@keyframes opacity-change__grass {\n  0% {\n    opacity: 0.5;\n  }\n  100% {\n    opacity: 1;\n  }\n}\n\n@keyframes brightness-change__horse {\n  0% {\n    filter: brightness(1%);\n  }\n  100% {\n    filter: brightness(100%);\n  }\n}\n\n@keyframes blur-change__bubble {\n  0% {\n    filter: blur(0);\n  }\n  100% {\n    filter: blur(10px);\n  }\n}\n\n@keyframes change-position__cloud {\n  from {\n    transform: translate(0, 50px);\n  }\n  to {\n    transform: translate(200px, 50px);\n  }\n}\n\n@keyframes change-position__cloud--reverse {\n  from {\n    transform: translate(446px, 48px);\n  }\n  to {\n    transform: translate(100px, 48px);\n  }\n}\n\n.works {\n  &-item {\n    width: 100%;\n    padding: 3rem;\n\n    &__title {\n      text-align: center;\n      margin-bottom: 1rem;\n    }\n\n    &__description {\n      margin-bottom: 3rem;\n      text-align: justify;\n    }\n\n    &__image {\n      margin: 0 auto;\n      height: 400px;\n\n      @media only screen and (max-width: 600px) {\n        width: 100%;\n        height: 100%;\n      }\n    }\n\n    &__techUsed {\n      margin-top: 2rem;\n      font-size: 1.5rem;\n\n      li {\n        margin: 0;\n        padding: 0;\n        font-weight: normal;\n        font-size: 1rem;\n      }\n    }\n\n    &__links {\n      float: right;\n\n      a {\n        margin-left: 1rem;\n        width: 50px;\n        height: 50px;\n\n        &:link {\n          color: #ffffff;\n        }\n\n        &:visited {\n          color: #ffffff;\n        }\n\n        &:hover {\n          color: red;\n          text-decoration: none;\n        }\n\n        &:active {\n          color: #808080;\n        }\n      }\n    }\n  }\n}\n\n.clearfix {\n  content: \"\";\n  display: table;\n  clear: both;\n}\n\n.navbar {\n  &-items {\n    &__top {\n      &--active {\n        background-color: #dedddd !important;\n        color: black;\n        font-weight: bold;\n      }\n\n      &--deactive {\n        font-style: italic;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
